	module.exports = {"data":{"markdownRemark":{"html":"<h1>August 03, 2016</h1>\n<ul>\n<li>Intro by Michelle</li>\n<li>\n<p><a href=\"https://twitter.com/ryanj?lang=en\">Ryan J</a> talked about \"Defining Applications in Kubernetes(and Openshift).</p>\n<ul>\n<li>His presentation can be found <a href=\"bit.ly/sig-apps-openshift\">here</a> and includes lots of helpful links.</li>\n<li>He went through some OpenShift primitives that are not in Kubernetes but may show up soon if the community sees a need.</li>\n<li>There was some in depth discussion about Deployments.</li>\n<li>Kubernetes has some concepts that came out of OpenShift Deployment Config.</li>\n<li>Q: What features are in Deployment Config that are high priority to push into Kubernetes Core at the moment?\nA: <em>(Clayton)</em> Yes. Handling deployment failures at a high level, a generic idea for a trigger controller which watches another system for changes and makes updates to a Deployment, and hooks.</li>\n<li>Ryan showed off OC which is a command line tool which is a wrapper for kubectl</li>\n<li>Comment: One of the challenges Kubernetes faces today is that there is not a great way to extensibly pull in new chunks of APIs. </li>\n<li>This is something that is actively being worked on today. This work is being discussed and worked on in <a href=\"/k8sC-sigs-demo/sig-api-machinery\">SIG-API-Machinery</a></li>\n<li>Free O'Reilly EBooks can be found <a href=\"http://gist-reveal.it/4ca683dff6cdb9601c495e27d4bb5289#/oreilly-ebooks\">here</a> courtesy of Red Hat.</li>\n</ul>\n</li>\n</ul>\n<p>Watch the <a href=\"https://youtu.be/8Gn44O6hSCw\">recording</a></p>"},"site":{"siteMetadata":{"sigs":["sig-api-machinery","sig-apps","sig-architecture","sig-auth","sig-autoscaling","sig-aws","sig-azure","sig-big-data","sig-cli","sig-cloud-provider","sig-cluster-lifecycle","sig-cluster-ops","sig-contributor-experience","sig-docs","sig-gcp","sig-ibmcloud","sig-instrumentation","sig-multicluster","sig-network","sig-node","sig-openstack","sig-product-management","sig-release","sig-scalability","sig-scheduling","sig-service-catalog","sig-storage","sig-testing","sig-ui","sig-vmware","sig-windows"]}}},"pathContext":{"slug":"/sig-apps/minutes/2016-08-03/"}}

